import React, { useEffect, useState } from 'react';
import { Form } from "react-bootstrap"
import Button from '@material-ui/core/Button';
import SearchVactions from '../../asyncfunction/searchVactions';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import moment from "moment"

const useStyles = makeStyles((theme) => ({
    root: {
        '& > *': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
}));

export default function SearchVacation(props) {

    const classes = useStyles();

    const keys = {
        destination: "",
        check_in_date: "",
        check_out_date: ""
    }

    const [searchValues, setValues] = useState(keys)
    const [locationMessage, setFirstMessage] = useState("none")
    const [dateMessage, setSecMessage] = useState("none")
    const [dayRange, setDayRange] = useState(null)

    async function searchAction() {
        if (!searchValues.destination) {
            setFirstMessage("block")
            return;
        }
        setFirstMessage("none")

        if ((searchValues.check_in_date && !searchValues.check_out_date) || (!searchValues.check_in_date && searchValues.check_out_date)) {
            setSecMessage("block")
            return;
        }
        setSecMessage("none")

        const result = await SearchVactions(searchValues);
        console.log(result)
        props.callback(result)
    }

    useEffect(() => {
        const dateString = searchValues.check_in_date.split("-")
        dateString[2]++
        const updatecheckOut = `${dateString[0]}-${dateString[1]}-${dateString[2]}`
        setValues({ ...searchValues, ["check_out_date"]: updatecheckOut })
    }, [searchValues.check_in_date])

    useEffect(() => {
        let time_start = new Date();
        let time_end = new Date();
        const value_start = searchValues.check_in_date.split('-');
        const value_end = searchValues.check_out_date.split('-');

        time_start.setFullYear(value_start[0])
        time_start.setMonth(value_start[1])
        time_start.setDate(value_start[2])

        time_end.setFullYear(value_end[0])
        time_end.setMonth(value_end[1])
        time_end.setDate(value_end[2])

        const diff = Number((time_end - time_start) / 86400000)
        diff ? setDayRange(diff) : setDayRange(null)

    }, [searchValues.check_out_date])


    function updateKeys(category, value) {
        setValues({ ...searchValues, [category]: value })

    }

    return (
        <div style={{ padding: "20px" }}>
            <div className="searchTab">
                <div style={{ paddingRight: "20px", fontSize: "30px" }}>
                    חיפוש
                </div>

                <Form style={{ padding: "20px", fontSize: "20px" }}>
                    <div style={{ color: "red", display: locationMessage }}>
                        נא לבחור יעד
                    </div>
                    <Form.Group >
                        <Form.Label>שם יעד/מקום אירוח</Form.Label>
                        <Form.Control style={{ textAlign: "right" }} className="formStyle" name="destination" type="text" placeholder=" ? לאן תרצו לנסוע" onChange={(e) => { updateKeys(e.target.name, e.target.value) }} />
                    </Form.Group>
                    <div style={{ color: "red", display: dateMessage }}>
                        נא לבחור תאריכים
                    </div>
                    <Form.Group>
                        <Form.Label>תאריך צ'ק-אין</Form.Label>
                        <TextField name="check_in_date" id="date" variant="outlined" type="date"
                            defaultValue={searchValues.check_in_date} className="formStyle" onChange={(e) => { updateKeys(e.target.name, e.target.value) }} />
                    </Form.Group>
                    <Form.Group>
                        <Form.Label>תאריך צ'ק-אווט</Form.Label>
                        <TextField name="check_out_date" id="date" variant="outlined" type="date"
                            value={searchValues.check_out_date} className="formStyle" onChange={(e) => { updateKeys(e.target.name, e.target.value) }} />
                    </Form.Group>
                    {dayRange && <div style={{ color: "red" }}>
                        { dayRange===1 && <span> שהייה של לילה אחד</span>}
                        { dayRange!==1 && <span> שהייה של {dayRange} לילות </span>}
                        
                    </div>}
                </Form>
                <div style={{ padding: "20px" }}>
                    <Button size="large" className="searchButton" variant="contained" color="primary" onClick={searchAction}>
                        <h4> חיפוש</h4>
                    </Button>
                </div>

            </div>
        </div>

    );
}